.main {
    overflow: hidden;
    position: relative;
    width: 100vw;
    height: 100vh;
    @include bgPosition('../img/main/bgMain.png');
    &_logo-section{
        align-self: start;
        margin: 61px 8% 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 40;
        .logo-img {
            width: 100%;
            height: auto;
            @media (max-height: 700px) {
                max-width: 70%;
                margin: 0 0 0 0;
            }
        }
        .logo-text {
            width: 312px;
            height: 36px;
            margin: 38px 0 0 0;
            @media (max-height: 700px) {
                width: 312px;
                height: 36px;
                margin: 10px 0 0 0;
            }
        }
        @media (max-height: 700px) {
            position: absolute;
            right: 3%;
            top: 0;
        }
    }
    &_hero-section {
        position: absolute;
        top: 10%;
        left: 11%;
        width: 45%;
        height: 100vh;
        display: flex;
        align-items: end;
        .hero {
            cursor: pointer;
            height: auto;
            object-fit: contain;
            max-width: 100%;
            @media (max-height: 700px) {
                max-width: 60%;
            }
        }
        .kru {
            z-index: 30;
            @media screen and (max-width: 1059px) {
                margin: 0 0 20% -50px; 
            }
            @media (max-height: 700px) {
                margin: 0 0 20% -20px;
            }
        }
        .porech {
            z-index: 20;
            margin: 150px 0 0 -200px;
            @media screen and (max-width: 1059px) {
                margin: 0 0 15% -40%; 
            }
            @media (max-height: 700px) {
                margin: 0 0 20% -60px;
            }
        }
        .woman {
            margin: 200px 0 0 -200px;
            @media screen and (max-width: 1059px) {
                margin: 0 0 5% -45%; 
            }
            @media (max-height: 700px) {
                margin: 0 0 10% -80px;
            }
        }   
        .hero_description {
            position: absolute;
            bottom: 15%;
            left: 50%;
            transform: translate(-10%, -50%);
            z-index: 300;
            width: 692px;
            height: auto;
            padding: 18px 14px 30px 14px;
            background: rgba(0, 0, 0, 0.35);
            border: 1px solid #971C1C;
            backdrop-filter: blur(6px);
            border-radius: 4px;
            .hero-title {
                margin: 0;
                font-family: 'SangBleu Sunrise';
                font-style: normal;
                font-weight: 450;
                font-size: 42px;
                line-height: 62px;
                text-align: center;
                letter-spacing: 0.03em;
                text-transform: uppercase;
                color: #FFE1DA;
                @media (max-width: 900px) {
                    font-size: 36px;
                    line-height: 62px;
                }
                @media (max-width: 900px) {
                    font-size: 24px;
                }
                @media (max-width: 633px) {
                    font-size: 18px;
                }
            }
            .hero-subtitle {
                margin: 0;
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 300;
                font-size: 24px;
                line-height: 28px;
                text-align: center;
                letter-spacing: 0.04em;
                color: #D3996D;
                @media (max-width: 900px) {
                    font-size: 20px;
                    // line-height: 62px;
                }
                @media (max-width: 900px) {
                    font-size: 18px;
                    line-height: 18px;
                }
                @media (max-width: 633px) {
                    font-size: 14px;
                    line-height: 14px;
                }
            }
            @media (max-width: 1440px) {
                bottom: 15%;
                left: 50%;
                transform: translate(-30%, -50%);
                width: 80vw;
            }
            @media (max-width: 900px) {
                bottom: 15%;
                left: 50%;
                transform: translate(-30%, -60%);
                width: 80vw;
            }
            @media (max-width: 728px) {
                padding: 5px 7px 10px 7px;
            }
            @media (max-width: 533px) {
                bottom: 45%;
                left: 50%;
                transform: translate(-30%, -40%);
            }
            @media (max-height: 700px) {
                bottom: 65px;
                right: 3%;
            }
        }
    }
    &_womanSvg {
        z-index: 200;
        position: absolute;
        right: 13%;
        bottom: 87px;
        @media (max-width: 1024px) {
            bottom: 25px;
        }
        @media (max-width: 800px) {
            width: 177px;
            height: 41px;
        }
        @media (max-height: 700px) {
            bottom: 65px;
            right: 3%;
        }
    }
    &_age {
        z-index: 200;
        position: absolute;
        left: 25px;
        bottom: 30px;
        @media (max-height: 700px) {
            width: 47px;
            height: 30px;
            bottom: 65px;
        }
    }
    &_blackout {
        z-index: 150;
        position: absolute;
        bottom: 0;
        width: 100vw;
        height: 147px;
        background: linear-gradient(180deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
        transform: rotate(-180deg);
    }
    &_decoration {
        display: flex;
        .decoration {
            z-index: 50;
            position: absolute;
            max-width: 100%;
            height: auto;
        }
        .stolb1 {
            z-index: 0;
            top: 0;
            left: 0;
        }
        .stolb2 {
            z-index: 0;
            top: 0;
            right: 0;
        }
        .keyart {
            bottom: 0;
            left: 0;
            width: 17%;
        }
        .down {
            bottom: 0;
            right: 0;
            width: 40%;
        }
        .owl {
            bottom: calc(100vh / 6);
            right: 0;
            width: calc(100% / 10);
            @media (max-width: 1024px) {
                bottom: 13%;
            }
            @media (max-width: 836px) {
                bottom: 10%;
            }
            @media (max-width: 612px) {
                bottom: 8%;
            }
        }
        .layer1 {
            bottom: 0%;
            left: 36%;
            width: 40%;
        }
        .layer2 {
            bottom: 0%;
            left: 10%;
            width: 40%;
        }
        .layer3 {
            bottom: 0%;
            left: 20%;
            width: 40%;
        }
    }
    &_link {
        position: absolute;
        z-index: 200;
        bottom: 36px;
        left: 50%;
        transform: translate(-50%, 0%);
        text-decoration: none;
        .select-hero {
            margin: 0;
            bottom: 36px;
            font-family: 'SangBleu Sunrise';
            font-style: normal;
            font-weight: 450;
            font-size: 42px;
            line-height: 62px;
            letter-spacing: 0.03em;
            text-transform: uppercase;
            color: #B58260;
            @media (max-width: 962px) {
                font-size: 36px;
            }
            @media (max-width: 825px) {
                font-size: 30px;
            }
            @media (max-width: 687px) {
                font-size: 24px;
            }
            @media (max-width: 550px) {
                font-size: 20px;
            }
        }
        @media (max-width: 1024px) {
            bottom: 106px;
        }
        @media (max-width: 728px) {
            bottom: 70px;
        }
        @media (max-height: 700px) {
            bottom: 70px;
        }
    }
    
}