.kvartira {
    overflow-x: hidden;
    position: relative;
    width: 100vw;
    height: 100vh;
    @include bgPosition('../img/kvartira/radji.webp');
    @media screen and (max-width: 800px) {
        @include bgPosition('../img/kvartira/radjiMobile.webp');
    }
    .card_text {
        .card_title {
            @media (max-height: 720px) {
                font-size: 36px;
                line-height: 30px;
                margin-bottom: 10px;
            }
        }
        
    }
    .card_dialog {
        grid-template-columns: 1fr;
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translate(-50%, -15%);
        width: 80vw;
        @media (max-width: 500px) {
            max-width: calc(100vw - 50px);
        }
        @media (max-height: 720px) {
            align-self: center;
            width: 80vw;
            bottom: -15%;
            // top: 30%;
        }
    }  
    .card_item {
        z-index: 100;
        cursor: pointer;
        position: absolute;
    }
    .card_mobile-img {
        @media screen and (max-width: 800px) {
            position: absolute;
            .card_item-mobile {
                max-width: 17%;
                top: 60%;
                left: 21%;
            }
        }
        @media (max-height: 700px) {
            width: 30%;
        }
        
    }
    .card_item-desctop {
        width: 164.56px;
        height: 164.56px;
        left: 4%;
        bottom: 17%;
        @media screen and (max-width: 800px) {
            display: none;
        }
    }
    .card_label {
        @media (max-height: 700px) {
            bottom: -15%;
        }
    }
}