.police {
    overflow-x: hidden;
    position: relative;
    width: 100vw;
    height: 100vh;
    @include bgPosition('../img/police/police.png');
    @media screen and (max-width: 800px) {
        @include bgPosition('../img/police/policeBgMobile.png');
    }
    .card_text {
        margin-top: 2%;
        margin-left: 7%;
        align-self: flex-start;
        .card_subtitle {
            text-align: center;
            max-width: 911px;
        }
    }
    .card_dialog {
        padding: 40px 60px 40px 0;
        grid-template-columns: 1fr 2fr;
    }
}