.map {
    overflow: scroll;
    padding-top: 50px;
    position: relative;
    width: 100vw;
    height: 100vh;
    @include bgPosition('../img/map/map.webp');
    background-size: cover;
    background-position: center;
    
    @media screen and (max-width: 800px) {
        @include bgPosition('../img/map/mobile_map.webp');
        background-size: cover;
        background-position: center;
    }
    .logo {
        display: block;
        margin: 0 auto;
        width: 331px;
        height: 180px;
        @media (max-height: 700px) {
            width: 20%;
            height: auto;
        }
    }
    .age {
        position: absolute;
        bottom: 8%;
        left: 15px;
    }
    .womanSvg {
        position: absolute;
        top: 50px;
        left: 50px;
        width: 257px;
        height: 57px;
        @media screen and (max-width: 800px) {
            display: none;
        }
    }
    .point_block {
        margin: 0 auto;
        width: 70vw;
        height: 100vh;
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate(-50%, 0%);
        .point-item {
            position: absolute;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .point-cirkle {
                width: 62px;
                height: 62px;
                background-color: #971C1C;
                border-radius: 50%;
                border: 20px solid #3C1515;
                @media screen and (max-width: 800px) {
                    border: 15px solid #3C1515;
                    width: 40px;
                    height: 40px;
                }
                @media screen and (max-width: 425px) {
                    border: 10px solid #3C1515;
                    width: 20px;
                    height: 20px;
                }
                @media (max-height: 700px) {
                    border: 15px solid #3C1515;
                    width: 30px;
                    height: 30px;
                }
            }
            .point-text {
                width: 271px;
                height: 82px;
                font-family: 'SangBleu Sunrise';
                font-style: normal;
                font-weight: 700;
                font-size: 28px;
                line-height: 41px;
                text-align: center;
                text-transform: uppercase;
                color: #FFFFFF;
                text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                @media screen and (max-width: 800px) {
                    font-size: 18px;
                }
                @media screen and (max-width: 425px) {
                    max-width: 100px;
                    font-size: 14px;
                    line-height: 20px;
                }
                @media (max-height: 700px) {
                    max-width: 100px;
                    font-size: 14px;
                    line-height: 20px;
                }
            }
            
        }
        .point-raji {
            top: 33%;
            left: 10%;
           @media (max-width: 1440px) {
                left: 5%;
           }
           @media (max-width: 1200px) {
                left: -10%;
           }
           @media (max-width: 1000px) {
                left: -15%;
           }
           @media (max-width: 800px) {
                top: 37%;
                left: -15%;
           }
           @media (max-width: 600px) {
                top: 39%;
                left: -20%;
           }
           @media (max-height: 700px) {
                top: 35%;
                left: -10%;
            }
           
        }
        .point-police {
            top: 23%;
            left: 60%;
            @media (max-width: 1000px) {
                left: 65%;
           }
            @media (max-width: 800px) {
                top: 31%;
                right: -5%;
           }
            @media (max-width: 600px) {
                top: 33%;
                right: -5%;
           }
            @media (max-width: 425px) {
                top: 37%;
                right: -15%;
           }
        }
        .point-traktir {
            top: 44%;
            left: 29%;
            @media (max-width: 1440px) {
                left: 25%;
           }
            @media (max-width: 1200px) {
                left: 19%;
           }
           @media (max-width: 1000px) {
                left: 13%;
            }
           @media (max-width: 800px) {
                top: 46%;
                left: 10%;
            }
           @media (max-width: 600px) {
                top: 47%;
                left: 0%;
            }
           @media (max-width: 425px) {
                top: 48%;
                left: 15%;
            }
            @media (max-height: 700px) {
                top: 46%;
                left: 24%;
            }
        }
        .point-house {
            top: 62%;
            right: 30%;
            @media (max-width: 1440px) {
                left: 68%;
           }
            @media (max-width: 1200px) {
                left: 70%;
           }
            @media (max-width: 1000px) {
                right: 10%;
           }
            @media (max-width: 800px) {
                top: 60%;
                right: 15%;
           }
            @media (max-width: 600px) {
                top: 59%;
                right: 15%;
           }
            @media (max-width: 425px) {
                top: 59%;
                right: 0%;
           }
           @media (max-height: 700px) {
                top: 65%;
                right: 0%;
            }
        }
        .point-antikvar {
            top: 74%;
            left: 15%;
            @media (max-width: 1440px) {
                left: 7%;
           }
            @media (max-width: 1200px) {
                left: 0%;
           }
            @media (max-width: 1000px) {
                left: -11%;
           }
            @media (max-width: 800px) {
                top: 68%;
                left: -15%;
           }
            @media (max-width: 600px) {
                top: 65%;
                left: -20%;
           }
           @media (max-height: 700px) {
                top: 70%;
                left: 0%;
            }
        }
    }
}