.card_block {
    display: flex;
    flex-direction: column;
    .card_text {
        max-width: 733px;
        align-self: flex-end;
        margin: 200px 200px 0 0;
        .card_title {
            position: relative;
            z-index: 50;
            font-family: 'Rubik Glitch';
            font-style: normal;
            font-weight: 400;
            font-size: 64px;
            line-height: 76px;
            text-align: center;
            text-transform: uppercase;
            color: #D3996D;
            
        }
        .card_subtitle {
            position: relative;
            z-index: 50;
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 300;
            font-size: 24px;
            line-height: 28px;
            text-align: center;
            letter-spacing: 0.04em;
            color: #D3996D;
            @media (max-height: 375px) {
                font-size: 14px;
                line-height: 14px;
            }
            @media (max-height: 720px) {
                margin-top: 15px;
                font-size: 18px;
                line-height: 18px;
            }
        }
        @media screen and (max-width: 1024px) {
            align-self: center;
            margin: 40px 0 0 0;
        }
        @media screen and (max-width: 800px) {
            margin: 40px 0 0 0;
            .card_title {
                font-size: 48px;
                line-height: 56px;
                margin-bottom: 0;
            }
            .card_subtitle {
                margin-top: 0;
                line-height: 23px;
            }
        }
        @media (max-height: 900px) {
            margin: 50px 50px 0 0;
        }
        @media (max-width: 900px) {
            margin: 10px 10px 0 0;
        }
        @media (max-height: 900px) {
            margin: 10px 10px 0 0;
        }
        @media (max-height: 720px) {
            align-self: center;
            width: 80vw;
        }
    }
    .card_dialog {
        margin: 50px auto;
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        justify-items: center;
        align-items: center;
        background: rgba(0, 0, 0, 0.35);
        border: 1px solid #971C1C;
        backdrop-filter: blur(6px);
        border-radius: 4px;
        max-width: 1000px;
        height: auto;
        .card_dialog-avatar {
            max-width: 100%;
        }
        .card_dialog-text {
            max-width: 669px;
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 300;
            font-size: 28px;
            line-height: 35px;
            text-align: center;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: #FFDBD3;
            @media screen and (max-width: 800px) {
                font-size: 14px;
                line-height: 20px;
            }
        }
        .card_dialog-item {
            max-width: 100%;
        }
        @media screen and (max-width: 1024px) {
            margin-top: 20px;
        }
        @media screen and (max-width: 800px) {
            margin-top: 200px;
            max-width: 700px;
            padding: 0;
        }
        @media (max-height: 720px) {
            top: 50%;
        }
    }
    .card_label {
        position: absolute;
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        width: 90vw;
        bottom: 2%;
        left: 30px;
        height: auto;
        .label {
            width: 241px;
            height: 132px;
            object-fit: contain;
            @media screen and (max-width: 800px) {
                width: 142px;
                height: 76px;
            }
        }
        .age {
            align-self: flex-end;
            width: 70px;
            height: 46px;
            object-fit: contain;
            @media screen and (max-width: 800px) {
                width: 47px;
                height: 30px;
            }
            @media (max-height: 720px) {
                margin-top: 125px;
            }
        }
        @media (max-height: 720px) {
            top: 125%;
            // align-items: center;
        }
    }
    .card_mobile-img {
        z-index: 0;
        position: absolute;
        left: 50%;
        top: 60%;
        transform: translate(-50%, -50%);
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        align-self: center;
        display: none;
        width: 100%;
        .mobile-img {
            margin: 0 auto;
            max-width: 90%;
        }
        @media screen and (max-width: 800px) {
            display: block;
            width: 50%;
            height: auto;
        }
        @media (max-height: 720px) {
            top: 85%;
        }
    }
    @media screen and (max-width: 425px) {
        justify-content: space-between;
        .card_text {
            .card_title {
                font-size: 36px;
                line-height: 48px;
            }
            .card_subtitle {
                font-size: 18px;
            }
        }
        .card_label {
            padding: 0 -10px 0 -10px;
        }
    }
}