.nochlejka {
    overflow-x: hidden;
    position: relative;
    width: 100vw;
    height: 100vh;
    @include bgPosition('../img/nochlejka/nochlejka.png');
    @media screen and (max-width: 800px) {
        @include bgPosition('../img/nochlejka/mobile_nochlejkaj.jpg');
    }
    .card_text {
        margin-top: 2%;
        margin-left: 7%;
        align-self: flex-start;
        .card_subtitle {
            text-align: center;
            max-width: 911px;
        }
    }
    .card_dialog {
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translate(-50%, 10%);
        width: 80vw;
        padding: 5px 10px 5px 0;
        grid-template-columns: 1fr 2fr;
        @media (max-width: 500px) {
            margin-top: 200px;
            max-width: calc(100vw - 50px);
        }
    }
}