.traktir {
    overflow-x: hidden;
    position: relative;
    width: 100vw;
    height: 100vh;
    @include bgPosition('../img/traktir/traktir.png');
    @media screen and (max-width: 800px) {
        @include bgPosition('../img/traktir/traktirMobile.png');
    }   
    .card_text {
        margin-top: 0%;
        .card_subtitle {
            text-align: center;
            // max-width: 911px;
        }
    }
    .card_dialog {
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translate(-50%, 10%);
        width: 80vw;
        grid-template-columns: 2fr 1fr;
        @media (max-width: 450px) {
            width: 95vw;
        }
    }
}