.antikvar {
    overflow-x: hidden;
    position: relative;
    width: 100vw;
    height: 100vh;
    @include bgPosition('../img/antikvar/bgAntikvar.png');
    @media screen and (max-width: 800px) {
        @include bgPosition('../img/antikvar/antikvar.png');
    }   
    .card_mobile-img {
        top: 50%;
    }
    .card_dialog {
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translate(-50%, 10%);
        width: 80vw;
        @media (max-width: 800px) {
            bottom: 10%;
            max-width: calc(100vw - 50px);
        }
    }
    
}